import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {PlusCircle} from "react-feather";
import {NumericFormat} from "react-number-format";
import Form from "../../../containers/form";
import {get, head} from "lodash";
import {Datepicker, Input, Select} from "../../../containers/form/components";
import {useGetAllQuery} from "../../../hooks/api";
import {getDefaultValue} from "../../../utils";
import dayjs from "dayjs";

const columns = [
    {
        title: 'ID',
        key: 'id',
        classnames: '!w-[80px] !min-w-0'
    },
    {
        title: 'Month',
        key: 'month',
    },
    {
        title: 'Organization level',
        key: 'organization_level',
    },
    {
        title: 'Organization type',
        key: 'organization_type',
    },
    {
        title: 'Amount by count',
        key: 'amount_by_count',
        render: ({value}) => <NumericFormat displayType={'text'} thousandSeparator={' '} value={value}/>
    },
    {
        title: 'Amount by group',
        key: 'amount_by_group',
        render: ({value}) => <NumericFormat displayType={'text'} thousandSeparator={' '} value={value}/>
    },
]
const BaseRatesContainer = () => {
    const [open, setOpen] = useState(false)
    const {data: levels} = useGetAllQuery({key: KEYS.organizationLevel, url: URLS.organizationLevel})
    const {data: types} = useGetAllQuery({key: KEYS.organizationType, url: URLS.organizationType})
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>Базовые ставки</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        onClick={() => setOpen(true)}
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <PlusCircle size={20} className={'mr-1.5'}/>
                        Добавить
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    <GridView
                        ModalBody={(onSubmit, defaultValues = {}) => <Form onSubmit={({data, ...rest}) => onSubmit({
                            data: {
                                ...data,
                                month: parseInt(dayjs(get(data, 'month')).format("MM")),
                                organization_level_id: get(data, 'organization_level_id.value'),
                                organization_type_id: get(data, 'organization_type_id.value')
                            }, ...rest
                        })}
                                                                           defaultValues={{
                                                                               ...get(defaultValues, 'data.data', {}),
                                                                               organization_level_id:getDefaultValue(get(levels, 'data', []).map(level => ({
                                                                                   value: get(level, 'id'),
                                                                                   label: get(head(get(level, 'translations', [])), 'name')
                                                                               })),parseInt(get(get(defaultValues, 'data.data', {}),'organization_level_id'))),
                                                                               organization_type_id:getDefaultValue(get(types, 'data', []).map(type => ({
                                                                                   value: get(type, 'id'),
                                                                                   label: get(head(get(type, 'translations', [])), 'name')
                                                                               })),parseInt(get(get(defaultValues, 'data.data', {}),'organization_type_id')))
                                                                           }}>
                            <Select name={'organization_level_id'} label={'Organization level'}
                                    params={{required: true}}
                                    options={get(levels, 'data', []).map(level => ({
                                        value: get(level, 'id'),
                                        label: get(head(get(level, 'translations', [])), 'name')
                                    }))}/>
                            <Select name={'organization_type_id'} label={'Organization type'} params={{required: true}}
                                    options={get(types, 'data', []).map(type => ({
                                        value: get(type, 'id'),
                                        label: get(head(get(type, 'translations', [])), 'name')
                                    }))}/>
                            <Input name={'amount_by_group'} property={{type: 'number', step: 1}}
                                   placeholder={'Amount by group'}
                                   label={'Amount by group'}
                            />
                            <Input name={'amount_by_count'} property={{type: 'number', step: 1}}
                                   placeholder={'Amount by count'}
                                   label={'Amount by count'}
                            />
                            <Datepicker property={{showMonthYearPicker: true}} dateFormat={'MM'} name={'month'}
                                        label={'Month'}
                                        params={{required: true}}/>
                            <button type={'submit'}
                                    className={'w-full p-3.5 rounded-lg bg-primary block w-full text-white font-bold text-center h-12 mt-6'}>
                                {open ? 'Create' : 'Save'}
                            </button>
                        </Form>}
                        openCreateModal={open}
                        setOpenCreateModal={setOpen}
                        hasActionColumn
                        listKey={KEYS.baseRates} url={URLS.baseRates}
                        columns={columns}/>
                </div>
            </div>
        </div>
    );
};

export default BaseRatesContainer;